<template>
  <div v-if="player!==undefined" class="player-detail">
    <div class="player-header"
         :style="{backgroundColor:`#${player.team.primaryColor}`,color:isBrightBg?'black':'white'}">
      <div style="display: flex;align-items: flex-end;">
        <div style="width: 700px;text-align: center">
          <img :src="player.basic.headshot" width="100%" class="player-img">
        </div>
        <div>
          <div class="player-name">{{player.basic.name}}</div>
          <div class="player-name-real">{{player.basic.givenName}} {{player.basic.familyName}}</div>

          <div style="display: flex;align-items: flex-end;margin: 2rem 0">
            <img :src="$teamLogos[player.team.id]" class="player-team-logo">
          </div>

        </div>
      </div>
    </div>
    <el-table
      :data="player.heroes"
      :default-sort="{prop: 'time_played_total', order: 'descending'}"
      style="width: 100%">
      <el-table-column
        prop="hero"
        label="英雄"
        sortable>
        <template slot-scope="scope">
          <div class="hero-portrait-image-mask">
            <div :style="`background-image: url(${getHeroIcon(scope.row.hero)})`" class="hero-portrait-image"></div>
          </div>
        </template>
      </el-table-column>
      <el-table-column
        prop="eliminations_avg_per_10m"
        label="消灭"
        :formatter="toFixed2"
        sortable>
      </el-table-column>
      <el-table-column
        prop="deaths_avg_per_10m"
        label="阵亡"
        :formatter="toFixed2"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="hero_damage_avg_per_10m"
        label="伤害"
        :formatter="toFixed0"
        sortable>
      </el-table-column>
      <el-table-column
        prop="healing_avg_per_10m"
        label="治疗"
        :formatter="toFixed0"
        sortable>
      </el-table-column>
      <el-table-column
        prop="ultimates_earned_avg_per_10m"
        :formatter="toFixed2"
        label="终极技能"
        sortable>
      </el-table-column>
      <el-table-column
        prop="final_blows_avg_per_10m"
        :formatter="toFixed2"
        label="最后一击"
        sortable>
      </el-table-column>
      <el-table-column
        prop="time_played_total"
        label="游戏时间"
        :formatter="formatTime"
        sortable>
      </el-table-column>
    </el-table>

  </div>
</template>

<script>
  import {convertSec, isBright, heroIcon} from "@/common/util"

  export default {
    name: "Player",
    data() {
      return {
        player: undefined,
        isBrightBg: undefined,
      }
    },
    mounted() {
      this.$owl.getPlayer(this.$route.params.id).then(player => {
          this.player = player;
          this.isBrightBg = isBright(player.team.primaryColor)
        }
      )
    },
    methods: {
      toFixed0(row, column, cellValue) {
        return cellValue.toFixed(0)
      },
      toFixed2(row, column, cellValue) {
        return cellValue.toFixed(2)
      },
      formatTime(row, column, cellValue) {
        const [h, m] = convertSec(cellValue);

        return h === 0 ?
          m === 0 ? `${Math.round(cellValue)}秒` : `${m}分钟`
          : `${h}小时${m}分钟`
      },
      getHeroIcon(heroName) {
        return heroIcon(heroName)
      }
    }
  }
</script>

<style scoped>
  .player-detail {
    max-width: 1100px;
    margin: 0 auto;
    text-align: left
  }

  .player-header {
    color: black
  }

  .player-header-dark {
    color: white
  }

  .player-img {
    max-width: 454px;
  }

  .player-team-logo {
    width: 10rem;
    height: 10rem;
  }

  .player-team-abbr {
    font-size: 1.2rem;
    margin-bottom: 0.5rem;
  }

  .player-team-full {
    font-size: 1rem
  }

  .player-name {
    font-size: 2.5rem;
  }

  .player-name-real {
    font-size: 1rem;
  }

  .hero-portrait-image-mask {
    width: 60px;
    height: 72px;
    background-color: rgba(51, 51, 51, .9);
    background-clip: padding-box;
    overflow: hidden;
    border-radius: 7px;
  }
  .hero-portrait-image {
    display: inline-block;
    width: calc(100% + 10px);
    height: 100%;
    background-size: auto 100%;
    background-position: 50% 50%;
    position: relative;

}

</style>



